/** 菜品信息  滚动条  begin  **/
#articleWrapper[data-v-6f2a5ddd]{
  overflow-y:scroll;
}
#articleWrapper[data-v-6f2a5ddd]::-webkit-scrollbar {/*滚动条整体样式*/
  width: 4px;     /*高宽分别对应横竖滚动条的尺寸*/
  height: 4px;
}
#articleWrapper[data-v-6f2a5ddd]::-webkit-scrollbar-thumb {/*滚动条里面小方块*/
  border-radius: 5px;
  -webkit-box-shadow: inset 0 0 5px rgba(0,0,0,0.2);
  background: rgba(0,0,0,0.2);
}
#articleWrapper[data-v-6f2a5ddd]::-webkit-scrollbar-track {/*滚动条里面轨道*/
  -webkit-box-shadow: inset 0 0 5px rgba(0,0,0,0.2);
  border-radius: 0;
  background: rgba(0,0,0,0.1);
}
/** 菜品信息  滚动条  end  **/


/** 菜品分类  滚动条  begin  **/
#familyWrapper[data-v-6f2a5ddd]{
  overflow-y:scroll;
}
#familyWrapper[data-v-6f2a5ddd]::-webkit-scrollbar {/*滚动条整体样式*/
  width: 4px;     /*高宽分别对应横竖滚动条的尺寸*/
  height: 4px;
}
#familyWrapper[data-v-6f2a5ddd]::-webkit-scrollbar-thumb {/*滚动条里面小方块*/
  border-radius: 5px;
  -webkit-box-shadow: inset 0 0 5px rgba(0,0,0,0.2);
  background: rgba(0,0,0,0.2);
}
#familyWrapper[data-v-6f2a5ddd]::-webkit-scrollbar-track {/*滚动条里面轨道*/
  -webkit-box-shadow: inset 0 0 5px rgba(0,0,0,0.2);
